import { Link } from "react-router-dom"
import { useState, useEffect } from 'react';
import { motion } from "framer-motion";

import styles from './Blog.module.css'
import { useTheme } from '../hooks/ThemeCtx'
import { pageVariants } from "../App";
import Tags from "../comps/tags"
import SectionTitle from '../comps/sectionTitle'

export default function Blog () {
  const { dark } = useTheme()
  const [ blogs, setBlogs ] = useState([])
  const [ data, setData ] = useState([])
  const [search, setSearch] = useState("")
  const [sortBy, setSortBy] = useState("")

  useEffect(() => {
    let ff = data.filter(blog => {
      return blog.title.includes(search.toLowerCase()) || blog.description.includes(search.toLowerCase())
    })
    ff.sort((a,b) => {
      switch(true) {
      case (sortBy === "date-d"):
        switch (true){
          case (Date.parse(a.date) < Date.parse(b.date)):
            return -1;
          case (Date.parse(a.date) > Date.parse(b.date)):
            return 1;
          default:
            return 0;
        }
      case (sortBy === "date-a"):
        switch (true){
        case (Date.parse(a.date) > Date.parse(b.date)):
            return -1;
        case (Date.parse(a.date) < Date.parse(b.date)):
            return 1;
          default:
            return 0;
        }
        default:
        return 1;
      }
    })
    setBlogs(ff)
  }, [data, data.length, search, sortBy])

  function getData () {
    fetch("/blogs.json")
      .then(response => {
        return response.json()
      })
      .then(obj => {
        setData(obj)
      })
  }

  useEffect (() => {
     getData()
  }, [])

  return (
  <motion.div className={styles.main}
    variants={pageVariants}
    initial="hidden"
    animate="show"
    exit="exit"
  >
      <div className={styles.options}>
        <div>
          <label htmlFor="sort">Sort by</label>
          <select id="sort" defaultValue={"default"} onChange={(e) => setSortBy(e.target.value)}>
            <option disabled value="default" > -- select an option -- </option>
            <option value="date-a">recent first</option>
            <option value="date-d">oldest first</option>
          </select>
        </div>
        <div>
          <label htmlFor="search">Search</label>
          <input id="search" value={search} onChange={(e) => setSearch(e.target.value)}/>
        </div>
      </div>

      <SectionTitle>Blog</SectionTitle>

        <motion.div className={styles.table} layout transition={{duration: 0.3}}>
        {
          blogs.map(blog => {
            return (
                    
            <div key = {blog.id} className={styles.card}
                style={{
                  color: dark ? "#aaa" : "#444",
                  backgroundColor: dark ? "#000" : "#fff",
                  border: dark ? "1px solid #444d" : "1px solid #dddd",
                }}
            >
                <div className={styles.image}
                    style={{
                        background: `url(${blog.image.link})`,
                    }}
                >
                </div>
                <Link to={`/blog/${blog.id}`}>
                    <h2 className={styles.title}>
                        {blog.title}
                    </h2>
                </Link>
                <Tags data={blog.tags} />
                <div className={styles.description}>
                    {blog.description}
                </div>
                <p className={styles.date}>
                    {blog.date}
                </p>
            </div>
            )
          })
        }
      </motion.div>
    </motion.div>
  )
}
